import urls from '../constants.js';
import {getBidAskDataWallet} from '../common.js';

export async function getBidEuroBitStamp() {
 let euroBid =  await getBidAskDataWallet(urls.bitstamp.btceur,true);
 return euroBid;
};

export async function getAskEuroBitStamp() {
  let euroAsk =  await getBidAskDataWallet(urls.bitstamp.btceur,false);
  return euroAsk;
};
